<template>
  <div style="background: rgba(0, 0, 0, 0.36)">
    <Card style="width:100%;background: rgba(0, 0, 0, 0.36);border: none;border-bottom: white 1px solid">
      <Icon type="ios-bulb"/>
      说明
      <span style="color: #ffabfa">菜单管理</span>
    </Card>
    <Tabs style="color:white;font-weight: bold">
      <TabPane label="菜单列表" icon="logo-apple">
        <Card :bordered="false" style="width: 100%;padding:0px;background: rgba(0, 0, 0, 0.36);margin-top: -15px;">
          <div style="margin:-5px 10px 10px 0px">
            <Button icon="md-add" type="default" shape="circle" style="float: right"
                    @click="saveOrUpdateMenuForm('add',{type:'1'})" ghost>增加
            </Button>
            <Input v-model="keyword" placeholder="搜索关键字" style="width: auto;width: 500px">
              <Button @click="searchMenus" slot="append" icon="ios-search"></Button>
            </Input>
          </div>
          <Table ref="table"
                 style="background: rgba(0, 0, 0, 0.36);"
                 :height="clientHeight+45"
                 :columns="menuInfoGrid.columns"
                 :row-class-name="rowClassName"
                 :data="menuInfoGrid.data" showStripe>

            <template slot-scope="{ row }" slot="name">
              <Row>
                <Col span="4" v-if="row.type==2">&nbsp;</Col>
                <Col span="8" v-if="row.type==3">&nbsp;</Col>
                <Col :span="16">
                  <div style="color: rgba(13,30,13,0.58);border-radius: 20px;line-height: 40px"
                       @click="showOrHide(row)"
                       :style="{color:function(){
                    if(row.type==2){return '#2d8cf0'}
                    else if(row.type==3){return 'green'}
                    else{return 'yellow'}
                }()}"
                  >
                    <Icon v-if="row.children.length>0" size="20"
                          style="color: lightblue"
                          :class="'treeActionBtn'+' drop'+row.id" type=ios-arrow-forward
                    ></Icon>
                    <Icon v-if="row.children.length==0" size="20"
                          style="color: rgba(0,0,0,0)"
                          type=md-arrow-dropright-circle></Icon>
                    <Icon v-if="row.icon.indexOf('/')==-1" size="30" :type=row.icon></Icon>
                    <img class="ivu-icon" style="border-radius: 5px" v-if="row.icon.indexOf('/')>-0" :src="'/api/file/'+row.icon" width="25" height="25"/>
                    &nbsp;&nbsp;
                    <strong>{{ row.name}}</strong>&nbsp;&nbsp;
                    <!--<strong v-if="row.children.length>0" style="color: white">{{"("+row.children.length+")" }}</strong>&nbsp;&nbsp;-->
                  </div>
                </Col>
              </Row>
            </template>
            <template slot-scope="{ row }" slot="state">
              <strong>{{$util.localDict.menuinfo.state[row.state]}}</strong>
            </template>
            <template slot-scope="{ row }" slot="type">
              <strong>{{$util.localDict.menuinfo.type[row.type]}}</strong>
            </template>
            <template slot-scope="{ row,index }" slot="action">
              <div style="text-align: right">
                <Icon v-if="row.type==2||row.type==1" size="25" style="color: #3862f5" type=md-add-circle
                      @click="saveOrUpdateMenuForm('add',{type:row.type,id:row.id})"
                ></Icon>
                <Icon size="25" style="color: lightblue" type=md-search @click="showMenuView(index)"></Icon>
                <Icon size="25" style="color: green" type=ios-create
                      @click="saveOrUpdateMenuForm('update',{type:row.type,id:row.id})"></Icon>
                <Icon size="25" style="color: red" type=ios-trash @click="removeMenu({id:row.id})"></Icon>
              </div>
            </template>
          </Table>
        </Card>
      </TabPane>
    </Tabs>
    <Drawer v-model="menuForm.show"
            draggable
            :closable="false"
            :mask-closable="false"
            width=1000>

      <div class="ivu-drawer-header">
        <Icon size="20" type="md-close" class="drawer-close-icon" @click="menuForm.show=false"/>
        <div class="ivu-drawer-header-inner">{{menuForm.title}}</div>
      </div>

      <Card :bordered="false" :style="{height:(clientHeight+160)+'px'}">
        <Form ref="menuInfoForm"
              :model="menuForm.data"
              :rules="menuForm.validate"
              enctype="multipart/form-data"
              :label-width="80">
          <FormItem label="名称" label-position="left" prop="name">
            <Input class="" v-model="menuForm.data.name" placeholder="please enter user name"/>
          </FormItem>
          <FormItem label="类型" label-position="left" prop="type">
            <Select v-model="menuForm.data.type" @on-change="changeType" placeholder="功能模块--模块菜单--页面权限">
              <Option value='1'>功能模块</Option>
              <Option value='2'>模块菜单</Option>
              <Option value='3'>页面权限</Option>
            </Select>
          </FormItem>
          <FormItem v-if="menuForm.showField.viewPath" label="页面路径" label-position="left">
            <Input class="" v-model="menuForm.data.viewPath" placeholder="视图路径"></Input>
          </FormItem>
          <FormItem v-if="menuForm.showField.groupName" label="分组名称" label-position="left">
            <Input class="" v-model="menuForm.data.groupName" placeholder="分组名称">
            </Input>
          </FormItem>
          <FormItem v-if="menuForm.showField.pid" label="父菜单" label-position="left" prop="pid">
            <Select v-model="menuForm.data.pid" placeholder="请选择父级" filterable>
              <Option v-for="(item,index) in menuForm.select.pid" :key="index" :value="item.id"><Icon size="30" :type="item.icon"/>{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="选择图标" label-position="left">
            <div style="height: 80px;width: 80px;display: block;border: 1px solid rgba(189,255,236,0.6);cursor: pointer;border-radius: 10px">
              <div style="position: relative;width: 260px">
                <img v-if="menuForm.data.icon.indexOf('blob:')>-1" :src="menuForm.data.icon" class="ivu-icon" style="border-radius: 10px" width="80" height="80"/>
                <img v-if="menuForm.data.icon.indexOf('/')>-1&&menuForm.data.icon.indexOf('blob:')==-1" :src="'/api/file/'+menuForm.data.icon" class="ivu-icon" style="border-radius: 10px" width="80" height="80"/>
                <Icon v-if="menuForm.data.icon.indexOf('/')==-1" size="80" :type="menuForm.data.icon" style=""/>
                <span @click="menuForm.iconSelect=true"
                      style="font-size:10px;width:70px;text-align:center;position: absolute;left: 85px;bottom:30px;background: #6660ff;line-height: 22px;border-radius: 5px">选择图标</span>
                <span style="position: absolute;width:70px;text-align:center;left: 85px;bottom: 0px;background: #ff5c4f;line-height: 22px;border-radius: 5px">
                  <Upload action=""
                          :headers="uploadHeader"
                          :format="['jpg','jpeg','png']"
                          :on-success="uploadSuccess"
                          :on-error="uploadError"
                          :before-upload="beforeUpload"
                          :max-size="948"
                          :show-upload-list="false">
                    上传图标
                  </Upload>
                </span>
                <span v-if="menuForm.uploadError" class="ivu-form-item-error-tip" style="margin-top: 5px;">超过文件大小限制,不能超过100KB!</span>
              </div>
            </div>
          </FormItem>
          <FormItem label="启用?" label-position="left">
            <i-switch true-value="0" false-value="1" size="large" v-model="menuForm.data.state">
              <span slot="open">启用</span>
              <span slot="close">禁用</span>
            </i-switch>
          </FormItem>
          <FormItem label="排序" label-position="left">
            <InputNumber :max="10000" style="width: 100%" :mix="1" v-model="menuForm.data.orderNum"></InputNumber>
          </FormItem>
          <FormItem label="描述" label-position="left">
            <Input type="textarea" v-model="menuForm.data.description" :rows="5"/>
          </FormItem>
        </Form>
        <div class="demo-drawer-footer" style="margin: auto">
          <Button style="margin-right: 8px" @click="menuForm.show = false">取消</Button>
          <Button type="primary" @click="saveOrUpdateMenu('menuInfoForm')">提交</Button>
        </div>
      </Card>
    </Drawer>

    <IconSelect
      v-on:iconSelectListen="selectIcon"
      :clientHeight="clientHeight"
      :show="menuForm.iconSelect"></IconSelect>

    <Drawer v-model="menuForm.menuView.show"
            draggable
            :closable="false"
            :mask-closable="false"
            width="1000">

      <div class="ivu-drawer-header">
        <Icon size="20" type="md-close" class="drawer-close-icon" @click="menuForm.menuView.show=false"/>
        <div class="ivu-drawer-header-inner">详情</div>
      </div>

      <Card :bordered="false" style="background: rgba(0, 0, 0, 0.16);height: 100%">
        <Row type="flex" v-for="(item ,index) in menuInfoGrid.columns"
             v-if="item.key"
             :key="index"
             style="height: 40px;margin-top: 5px;background: rgba(4,9,7,0.2);line-height: 35px;border-radius: 6px">
          <Col span="4">
            <div style="color: rgb(167,225,230);padding: 5px;text-align: center;font-weight: bold">
              {{item.title}}
            </div>
          </Col>
          <Col span="20">
            <div style="color: rgba(159,216,209,0.65);padding: 5px;">
              <div v-if="item.key=='icon'">
                <Icon size="30" :type="menuForm.menuView.data[item.key||item.slot]"/>
              </div>
              <div v-else>
                <span v-if="menuForm.menuView.data[item.key||item.slot]" style="color: yellow">
                  {{(menuForm.menuView.data[item.key||item.slot])}}
                </span>
                <span v-else>
                  ####################
                </span>
              </div>
            </div>
          </Col>
        </Row>
      </Card>
    </Drawer>
  </div>
</template>

<script>
  import IconSelect from "../../components/IconSelect";

  export default {
    components: {IconSelect},
    props: ["clientHeight"],
    data() {
      return {
        menuIndex: {},
        menuIndexData: {},
        keyword: "",
        uploadHeader:{
          token:sessionStorage.getItem('token')
        },
        styles: {
          height: 'calc(100% - 55px)',
          overflow: 'auto',
          paddingBottom: '53px',
          position: 'static'
        },
        menuInfoGrid: {
          columns: [
            // {
            //   type: 'selection',
            //   width: 60,
            //   align: 'center',
            //   fixed: 'left',
            // },
            {
              title: '名称',
              slot: 'name',
              key: 'name',
              width: 350,
              fixed: 'left',
            },
            {
              title: '操作',
              slot: 'action',
              width: 150,
              fixed: 'left',
              align: 'center'
            },
            // {
            //   title: '图标',
            //   key: 'icon',
            //   width: 150
            //
            // },
            {
              title: '状态',
              key: 'state',
              slot: 'state',
              width: 150
            },
            {
              title: '排序',
              key: 'orderNum',
              width: 150
            },
            {
              title: '类型',
              key: 'type',
              slot: 'type',
              width: 150,
              filters: [
                {
                  label: '功能模块',
                  value: 1
                },
                {
                  label: '模块菜单',
                  value: 2
                },
                {
                  label: '菜单权限',
                  value: 3
                }
              ],
              filterMethod(value, row) {
                if (value === 1) {
                  return row.type === '1';
                } else if (value === 2) {
                  return row.type === '2';
                } else if (value === 3) {
                  return row.type === '3';
                }
              }
            },
            {
              title: '所属组',
              key: 'groupName',
              width: 150
            },
            {
              title: '视图路径',
              key: 'viewPath',
              width: 150
            },
            {
              title: '创建人',
              key: 'createUserId',
              width: 150
            },
            {
              title: '创建时间',
              key: 'createTime',
              width: 150
            },
            {
              title: '修改人',
              key: 'updateUserId',
              width: 150
            },
            {
              title: '修改日期',
              key: 'updateTime',
              width: 150
            }],
          data: [],
          pageData: {
            total: 100,
            current: 1,
            size: 10
          }
        },
        menuForm: {
          title: "添加",
          iconSelect: false,
          showField: {
            groupName: false,
            pid: false,
            viewPath: false
          },
          select: {
            pid: []
          },
          clearData: {
            id: '',
            name: '',
            pid: '',
            viewPath: '',
            type: '1',
            state: '0',
            orderNum: 1,
            groupName: '',
            icon: 'md-help',
            description: '',
            desc: ''
          },
          uploadError:false,
          data: {
            id: '',
            name: '',
            pid: '',
            viewPath: '',
            type: '1',
            state: '0',
            icon: 'md-help',
            iconFile:null,
            orderNum: 1,
            groupName: '',
            description: '',
            desc: ''
          },
          validate: {
            name: [
              {required: true, message: '请输入名称', trigger: 'blur'},
            ],
            pid: [
              {required: true, message: '请选择父菜单', trigger: 'change'}
            ],
            type:
              [
                {required: true, message: '请选择类型', trigger: 'change'}
              ]
          },
          show: false,
          menuView: {
            data: {},
            show: false
          }
        }
      }
    },
    methods: {
      searchMenus() {
        if (this.keyword == "") {
          this.showOrHideRow(this.menuInfoGrid.data, true)
          this.$jq(".row-0").show()
        } else {
          this.showOrHideRow(this.menuInfoGrid.data, true)
          let keyword = this.keyword
          for (let key in this.menuIndex) {
            if (key.indexOf(keyword) > -1) {
              let item = this.menuIndex[key]
              this.$jq(".search-" + item.id).show()
              this.cascadParentMenuShow(this.menuIndexData[item.pid])
            }
          }
        }
      },
      cascadParentMenuShow(menu) {
        if (menu) {
          this.$jq(".search-" + menu.id).show()
          this.$jq('.drop' + menu.id).addClass('ivu-icon-ios-arrow-down').removeClass('ivu-icon-ios-arrow-forward')
          this.cascadParentMenuShow(this.menuIndexData[menu.pid])
        }
      },
      rowClassName(row, index) {
        this.menuIndexData[row.id] = row
        this.menuIndex[row.name + "__" + index] = row
        if (row.type == '2' || row.type == "3") {
          row.closed = true
          return 'row-' + row.pid + " row-hidden" + " " + "search-" + row.id
        } else {
          row.closed = false
          return 'row-' + row.pid + " " + "search-" + row.id
        }
      },
      showOrHide(row) {
        //ivu-icon-ios-arrow-forward
        //ivu-icon-ios-arrow-down
        if (row.closed == undefined) {
          row.closed = true;
        }
        if (row.closed) {
          this.showOrHideRow(row.children, false)
          this.$jq('.drop' + row.id).addClass('ivu-icon-ios-arrow-down').removeClass('ivu-icon-ios-arrow-forward')
          row.closed = false
        } else {
          this.showOrHideRow(row.children, true)
          this.$jq('.drop' + row.id).addClass('ivu-icon-ios-arrow-forward').removeClass('ivu-icon-ios-arrow-down')
          row.closed = true
        }
      },
      showOrHideRow(children, hide) {
        if (children.length > 0) {
          for (let i in children) {
            let item = children[i]
            if (hide) {
              this.$jq(".row-" + item.pid).hide()
              //级连关闭
              this.$jq('.drop' + item.id).addClass('ivu-icon-ios-arrow-forward').removeClass('ivu-icon-ios-arrow-down')
              this.showOrHideRow(item.children, hide)
            } else {
              this.$jq(".row-" + item.pid).show()
            }
          }
        }
      },
      showMenuView(index) {
        this.menuForm.menuView.show = true;
        this.menuForm.menuView.data = this.menuInfoGrid.data[index]
      },
      removeMenu(param) {
        const that = this;
        that.$Modal.confirm({
          title: "警告",
          content: "确定要删除吗？",
          onOk: function () {
            that.$util.req("/api/menuService/delMenu")
              .setJsonDataStr({ids: param.id})
              .setSuccessfn(function (result) {
                if (result.code === 1000) {
                  that.$Notice.success({
                    title: '操作成功',
                    desc: '菜单删除成功'
                  });
                  that.searchMenuList()
                }
              }).go(that)
          }
        })
      },
      changeType(value, callBack) {
        callBack = callBack || function () {
        }
        if (value == "1") {
          this.menuForm.showField.pid = false
          this.menuForm.showField.viewPath = false
          this.menuForm.showField.groupName = false
          this.menuForm.data.pid = '0'
          callBack()
        } else if (value == "2" || value == "3") {
          if (value == "2") {
            this.menuForm.showField.groupName = true
          }
          if (value == "3") {
            this.menuForm.showField.groupName = false
          }
          this.menuForm.showField.pid = true
          this.menuForm.showField.viewPath = true
          const that = this;
          that.$util.req("/api/menuService/getParentMenuList")
            .setJsonDataStr({type: value})
            .setSuccessfn(function (result) {
              that.menuForm.select.pid = result.data
              callBack()
            })
            .go(that)
        }
      },
      selectIcon: function (fun) {
        fun(this)
        this.menuForm.data.iconFile=null
      },
      uploadSuccess(response, file, fileList){
        let fileInfo = response.data
        this.menuForm.data.icon=fileInfo[0]+"/"+fileInfo[1]
        console.log(response, file, fileList)
      },
      uploadError(response, file){
        let that = this;
        if(file.statusCode==="201"){
          that.$Modal.warning({
            title: "登录已过期",
            content: "请重新登录",
            width: 400,
            onOk: function () {
              sessionStorage.setItem("history", that.$route.path);
              that.$router.push({path: "/login"})
            }
          })
        }
      },
      beforeUpload(file){
        if(file.size<=100000){
          this.menuForm.uploadError=false
          this.menuForm.data.iconFile=file
          let imgURL = URL.createObjectURL(file);
          this.menuForm.data.icon=imgURL
        }else {
          this.menuForm.uploadError=true
        }
        return false
      },
      saveOrUpdateMenuForm(actiontype, data) {
        if (actiontype === "add") {
          this.menuForm.title = "添加"
          this.menuForm.data = this.menuForm.clearData
          this.changeType(data.type)
        } else if (actiontype === "update") {
          this.menuForm.title = "修改"
          const that = this;
          that.$util.req("/api/menuService/searchMenu")
            .setJsonDataStr({id: data.id})
            .setSuccessfn(function (result) {
              const menuInfo = result.data
              console.log(menuInfo)
              that.changeType(menuInfo.type, function () {
                that.menuForm.data = menuInfo
              })
            })
            .go(that)
        }
        this.menuForm.show = true
      },
      saveOrUpdateMenu(name) {
        let formData = new FormData();
        this.$refs[name].validate((valid) => {
          if (valid) {
            formData.append("menuInfo",encodeURI(JSON.stringify(this.menuForm.data)))
            formData.append("iconFile",this.menuForm.data.iconFile)
            const that = this;
            that.$jq.ajax({
              url:'/api/menuService/saveOrUpdate',
              dataType:'json',
              type:'POST',
              async: false,
              beforeSend: function (request) {
                  request.setRequestHeader('token', sessionStorage.getItem("token"));
              },
              data: formData,
              processData : false, // 使数据不做处理
              contentType : false, // 不要设置Content-Type请求头
              success: function(result){
                if (result.code === 1000) {
                  that.$Notice.success({
                    title: '操作成功',
                    desc: '菜单' + that.menuForm.title + '成功'
                  });
                  that.searchMenuList();
                  that.menuForm.show = false;
                }
              },
              error:function(response){
                console.log(response);
              }
            });
            // that.$util.req("/api/menuService/saveOrUpdate")
            //   .setJsonDataStr(that.menuForm.data)
            //
            //   .setSuccessfn(function (result) {
            //     if (result.code === 1000) {
            //       that.$Notice.success({
            //         title: '操作成功',
            //         desc: '菜单' + that.menuForm.title + '成功'
            //       });
            //       that.searchMenuList();
            //       that.menuForm.show = false;
            //     }
            //   })
            //   .setErrorfn(function (e) {
            //   })
            //   .go(this)
          } else {
            //this.$Message.error('Fail!')
          }
        })
      },
      searchMenuList() {
        const that = this;
        that.$util.req("/api/menuService/searchMenuTreeSortList")
          .setJsonDataStr({})
          .setSuccessfn(function (result) {
            if (result.success) {
              that.menuInfoGrid.data = result.data
              //that.menuInfoGrid.pageData.current = result.data.current;
              //that.menuInfoGrid.pageData.total = result.data.total;
            }
          })
          .setErrorfn(function (e) {
          })
          .go(that)
      },
      changePageNum(pageNum) {
        this.menuInfoGrid.pageData.current = pageNum;
        this.searchMenuList()
      },
      changePageSize(pageSize) {
        this.menuInfoGrid.pageData.size = pageSize;
        this.searchMenuList()
      }
    },
    mounted() {
      this.searchMenuList();
    }
  }
</script>

<style>
  .row-hidden {
    display: none;
  }
</style>



